








































	export default {
		data() {
			return {
				SessionKey: '',
				OpenId: '',
				code: "",
				nickName: null,
				avatarUrl: null,
				isCanUse: uni.getStorageSync('info') || true, //默认为true
				getphone: "1",
				writePhone: false,
				phoneNumber: "", //手机号
			};
		},
		methods: {
			//第一授权获取用户信息===》按钮触发
			wxGetUserInfo(detail) {

                
				let _this = this;
				 _this.setOpenid()
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						 console.log("phon",infoRes)
						_this.nickName = infoRes.userInfo.nickName; //昵称
						_this.avatarUrl = infoRes.userInfo.avatarUrl; //头像

						try {
							//uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
							// _this.updateUserInfo();

						} catch (e) {}
					},
					fail(res) {}
				});
			},



			// //登录
			login() {
				let _this = this;
				uni.showLoading({
					title: '登录中...'
				});

				// 1.wx获取登录用户code
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						console.log("loginRes",loginRes)
						_this.code = loginRes.code;
						
						//	2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息 
					

						uni.hideLoading()
						

                    },
				});
			},
			setOpenid(){
				var _this=this;

					uni.request({
					url: this.config.url+"getOpenid",
					data: {
						code: _this.code,
					},
					method: 'POST',
					success: (res) => {
					  console.log("getopenid",res)
				            if(res.data.code==2){
								_this.OpenId = res.data.data.openid;
								console.log("_this.Openid",_this.Openid);
								//_this.getinfo();
								_this.SessionKey = res.data.data.session_key;
							}
						
				
						uni.setStorage({
							key: "sessionkey",
							data: _this.SessionKey
						})
						//openId、或SessionKdy存储//隐藏loading
				
					}
				});
				
			},
			//向后台更新信息
			updateUserInfo() {
				this.setOpenid();
			},
			getinfo(){
				let _this = this;
					uni.request({
						url: _this.config.url + "reg", //服务器端地址
						data: {
							openid: _this.OpenId,
							headimg: _this.avatarUrl,
							nickname: _this.nickName
						},
						method: 'POST',
				
						success: (res) => {
							console.log( "22",res)
							if (res.data.code == 1) {
								var info = JSON.stringify(res.data.data)
								uni.setStorage({
									key: "info",
									data: info,
									success: (res) => {
										console.log("用户信息保存成功")
									}
								})
				
							}
						}
				
					});
				
			},
			cancel() {
				this.writePhone = false;

			},
			//确认跳转home
			confirm() {
				console.log(1)
				var _this = this;
				var result = this.phoneNumber.replace(/(^\s+)|(\s+$)/g, "");
				if (result.length < 1) {
					console.log(2)
					uni.showToast({
						title: "请输入手机号",
						duration: 1000
					})
				} else if (!(/^1[3456789]\d{9}$/.test(this.phoneNumber))) {
					console.log(3)
					uni.showToast({
						title: "请输入正确的手机号",
						duration: 1000
					})
				} else {
					console.log(4)
					uni.request({
						url: _this.config.url + "reg",
						method: "POST",
						data: {
							openid: this.OpenId,
							headimg: this.avatarUrl,
							nickname: this.nickName,
							phone: this.phoneNumber
						},
						success: (res) => {
							console.log("reg", res)
							if (res.data.code == 1) {
								var userinfo = JSON.stringify(res.data.data)
								uni.setStorage({
									key: "info",
									data: userinfo,
									success: () => {
										uni.reLaunch({
											url: "/pages/tabBar/home"
										})
									}
								})
							}
						}
					})
				}
			}
		},
		onLoad() { //默认加载

			uni.getStorage({
				key: "info",
				success: (res) => {
					uni.reLaunch({
						url: "/pages/tabBar/home"
					})

				},
				fail: () => {

					this.login();
				}
			})

		}
	}
